@import './../common/styles/sass/variables';
@import './../common/styles/sass/mixins';

.mainBlock {
  width: 100%;
  height: 100vh;
  background: url("./../assets/img/main-bg.jpg") top center;
  background-size: cover;
  text-align: center;

  &:before {
    content: "";
    background: rgba(5, 13, 24, 0.3);
    position: absolute;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
  }

  .mainContainer {
    @include container();
    @include flex(row, center, center, nowrap);

    .greeting {
      min-width: 300px;
      position: relative;
      z-index: 2;

      h2 {
        margin: 0 0 50px 0;
        font-size: 72px;
        font-weight: 700;
        line-height: 56px;
        color: $activeTextColor;
        font-family: "Raleway", sans-serif;
      }

      p {
        color: $activeTextColor;
        margin-bottom: 50px;
        font-size: 38px;
        font-weight: 450;
        font-family: "Poppins", sans-serif;

        span {
          color: #fff;
          padding-bottom: 4px;
          letter-spacing: 1px;
          border-bottom: 3px solid #149ddd;
        }

      }
    }
  }

}

@media (min-width: 1024px) {
  .mainBlock {
    background-attachment: fixed;
  }
}

@media (max-width: 768px) {
  .mainBlock h2 {
    font-size: 28px;
    line-height: 36px;
  }
}


